import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, XSquare, CheckSquare, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
// import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../ClientSideDataTable'
import { toast } from 'react-toastify'

const ApproveBulkPurchase = () => {
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [pendingBulkPurchaseList, setpendingBulkPurchaseList] = useState([])
    const created_by = JSON.parse(localStorage.getItem('userData')).id
    const idx = JSON.parse(localStorage.getItem('userData')).idx

    useEffect(() => {
        localStorage.setItem('usePMStoken', true)
        useJwt.bulkPurchaseList({ created_by, idx }).then(res => {
            console.log(res)
            setpendingBulkPurchaseList(res.data.payload)
            localStorage.setItem('usePMStoken', false)
            setTableDataLoading(false)
        }).catch(err => {
            // Error(err)
            console.log(err.response)
            setTableDataLoading(false)
            localStorage.setItem('usePMStoken', false)
        })
    }, [resetData])
    const handlePoPupActions = (id, action_id, message) => {
        localStorage.setItem('usePMStoken', true)
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // console.log({ id, action_id })
                return useJwt.bulkPurchaseApprove({ id, action_id }).then(res => {
                    // Success(res)
                    toast.success(res.data.message)
                    console.log(res)
                    setpendingBulkPurchaseList(pendingBulkPurchaseList.filter(x => x.id !== id))
                    localStorage.setItem('usePMStoken', false)
                }).catch(err => {
                    localStorage.setItem('usePMStoken', false)
                    console.log(err.response)
                    toast.error(res.data.message)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'SL',
            maxWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Group Name',
            maxWidth: '160px',
            sortable: true,
            selector: 'group_name'
        },
        {
            name: 'Title',
            maxWidth: '170px',
            sortable: true,
            selector: 'title'
        },
        {
            name: 'Body',
            maxWidth: '400px',
            sortable: true,
            selector: 'body'
        },
        {
            name: 'Action',
            maxWidth: '50px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Approve">
                        <CheckSquare size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 1, 'Do you want to approve?')}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Reject">
                        <XSquare size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 0, 'Do you want to reject?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Bulk Purchase List</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={pendingBulkPurchaseList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default ApproveBulkPurchase